
html, #app {
    height: 100%;
    overflow: hidden;
}
body {
    position: relative;
    height: 100%;
    max-height: 100%;
    margin: 0;
    padding: 0;
    border: 0;
}
.fade-enter-active, .fade-leave-active {
    transition: opacity .5s
}
.fade-enter, .fade-leave-active {
    opacity: 0
}



